:root {
    --primary-color: #2584ff;
    --secondary-color: #00d9ff;
    --accent-color: #ff3400;
    --headings-color: #1b0760;
    --body-color: #918ca4;
    --body-color-dark: #5c5577;
    --border-color: #ccc;
    --border-radius: 30px;

}

*,
*::after,
*::before {
    box-sizing: border-box;
}

/*  Typography  */
::selection {
    background: var(--primary-color);
    color: #fff;
}

html {
    font-size: 62.5%;

}

body {
    font-family: Inter, Arial, Helvetica, sans-serif;
    color: var(--body-color);
    font-size: 2.4rem;
    line-height: 1.5;
}

h1,
h2,
h3 {
    color: var(--headings-color);
    margin-bottom: 1rem;
    line-height: 1.1;

}

h1 {
    font-size: 7rem;
}

h2 {
    font-size: 4rem;
}

h3 {
    font-size: 3rem;
    font-weight: 500;
}

.block--dark h1,
.block--dark h2,
.block--dark h3 {
    color: #fff;
}

p {
    margin-top: 0;
}

@media screen and (min-width:1024px) {
    body {
        font-size: 1.8rem;
    }

    h1 {
        font-size: 8rem;
    }

    h2 {
        font-size: 4rem;
    }

    h3 {
        font-size: 2.4rem;
    }

}

/* Links  */
a {
    text-decoration: none;
}

.link-arrow {
    color: var(--accent-color);
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: bold;
}

.link-arrow::after {
    content: '-->';
    margin-left: 5px;
    transition: all 0.2s ease;
    font-weight: 600;
}

.link-arrow:hover::after {
    margin-left: 10px;
}

@media screen and (min-width:1024px) {
    .link-arrow {
        font-size: 1.5rem;
    }
}

/* Badges */
.badge {
    border-radius: 20px;
    font-size: 2rem;
    font-weight: 600;
    padding: 0.5rem 2rem;
    white-space: nowrap;
}

.badge--small {
    font-size: 1.6rem;
    padding: 0.5rem 1.5rem;
}

.badge--primary {
    background-color: var(--primary-color);
    color: #fff;
}

.badge--secondary {
    background-color: var(--secondary-color);
    color: #fff;
}

.badge--accent {
    background-color: #ec3000;
}

@media screen and (min-width:1024px) {
    .badge {
        font-size: 1.5rem;
    }

    .badge--small {
        font-size: 1.2rem;
    }
}

/* Lists */
.list {
    list-style: none;
    padding: 0;
}

.list--inline .list__item {
    display: inline-block;
    margin-right: 2rem;
    color: var(--headings-color);
}

.list--tick {
    list-style-image: url('../images/tick.svg');
    padding-left: 3rem;
}

.list--tick .list__item {
    padding-left: 0.5rem;
    margin-bottom: 1rem;
}

@media screen and (min-width:1024px) {
    .list--tick .list__item {
        padding-left: 0;
    }
}

/* Icons */
.icon {
    width: 40px;
    height: 40px;
}

.icon--small {
    width: 30px;
    height: 30px;
}

.icon--primary {
    fill: var(--primary-color);
}

.icon--light {
    fill: #fff;
}

.icon-container {
    align-items: center;
    background-color: #f3f9fa;
    border-radius: 50%;
    display: inline-flex;
    width: 64px;
    height: 64px;
    justify-content: center;
}

.icon-container--accent {
    background-color: var(--accent-color);
}

/* Icons  */
.btn {
    border-radius: 40px;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 1.8rem;
    font-weight: 600;
    margin: 1rem 0;
    outline: none;
    padding: 2rem 4vw;
    text-transform: uppercase;
    text-align: center;
    white-space: nowrap;

}

.btn .icon {
    width: 2rem;
    height: 2rem;
    margin-right: 0.5rem;
    vertical-align: middle;
}

.btn--primary {
    background-color: var(--primary-color);
}

.btn--primary:hover {
    background-color: #3a8ffd;
}

.btn--secondary {
    background-color: var(--secondary-color);
}

.btn--secondary:hover {
    background-color: #00c8eb;
}

.btn--accent {
    background-color: var(--accent-color);
}

.btn--accent:hover {
    background-color: #ec3000;
}

.btn--stretched {
    padding: 2rem 6rem;

}

.btn--block {
    width: 100%;
    display: inline-block;
}

.btn--outline {
    color: var(--headings-color);
    border: 2px solid var(--headings-color);
}

.btn--outline:hover {
    color: #fff;
    background: var(--headings-color);
}

@media screen and (min-width:1024px) {
    .btn {
        font-size: 1.5rem;
    }
}

/* Inputs */
.input {
    border-radius: var(--border-radius);
    border: 1px solid var(--border-color);
    color: var(--headings-color);
    font-size: 2rem;
    outline: none;
    padding: 1.5rem 3.5rem;
}

::placeholder {
    color: #cdcbd7;
}

.input-group {
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius);
    display: flex;

}

.input-group .input {
    border: none;
    flex-grow: 1;
    padding: 1.5rem 2rem;
    width: 0;
}

.input-group .btn {
    margin: 4px;

}

.input-group .icon {
    fill: red;
}

@media screen and (min-width:1024px) {
    .input {
        font-size: 1.5rem;
    }
}

/* Cards */
.card {
    border-radius: 7px;
    box-shadow: 0 0 20px 10px #f3f3f3;
    overflow: hidden;
}

.card__header,
.card__body {
    padding: 2rem 3rem;
}

.card--primary .card__header {
    background-color: var(--primary-color);
    color: #fff;
}

.card--secondary .card__header {
    background-color: var(--secondary-color);
    color: #fff;
}

.card--primary .badge--primary {
    background-color: #126de4;

}

.card--secondary .badge--secondary {
    background-color: #02cdf1;
}

.card--accent .card__header {
    background-color: var(--accent-color);
    color: #fff;
}



/* Plans */
.plan {
    transition: transform 0.2s ease-out;
}

.plan__name {
    color: #fff;
    margin: 0;
    font-weight: 500;
    font-size: 2.4rem;
}

.plan__price {
    font-size: 6rem;
}

.plan__billing-cycle {
    font-size: 2.4rem;
    font-weight: 300;
    opacity: 0.8;
    margin-right: 1rem;
}

.plan__description {
    font-size: 2rem;
    font-weight: 300;
    letter-spacing: 1px;
    display: block;
}

.plan .list__item {
    margin-bottom: 2rem;
    color: var(--headings-color);

}

.plan--popular {
    transform: scale(1.1);
}

.plan:hover {
    transform: scale(1.05);
}

.plan--popular:hover {
    transform: scale(1.15);
}

.plan--popular .card__header {
    position: relative;
}

.plan--popular .card__header::before {
    content: url('/images/popular.svg');
    width: 40px;
    display: inline-block;
    position: absolute;
    top: -6px;
    right: 5%;
}

@media screen and (min-width:1024px) {
    .plan__name {
        font-size: 1.4rem;
    }

    .plan__price {
        font-size: 5rem;
    }

    .plan__billing-cycle {
        font-size: 1.6rem;
    }

    .plan__description {
        font-size: 1.7rem;
    }
}

/* Media Objects */
.media {
    display: flex;
    margin-bottom: 4rem;
}

.media__title {
    margin-top: 0;
}

.media__body {
    margin: 0 2rem;
}

.media__image {
    margin-top: 1rem;
}

/* Quotes */

.quote {
    font-size: 3rem;
    font-style: italic;
    color: var(--body-color-dark);
    line-height: 1.3;
}

.quote__text::before {
    content: open-quote;
}

.quote__text::after {
    content: close-quote;
}

.quote__author {
    font-size: 3rem;
    font-weight: 500;
    font-style: normal;
    margin-bottom: 0;
}

.quote__company {
    color: var(--headings-color);
    font-size: 2rem;
    opacity: 40%;
    font-style: normal;
}

.quote__line {
    position: relative;
    bottom: 10px;
}

@media screen and (min-width:1024px) {
    .quote {
        font-size: 2rem;
    }

    .quote__author {
        font-size: 2.4rem;
    }

    .quote__company {
        font-size: 1.6rem;
    }
}

/* Grid */
.grid {
    display: grid;
}

@media screen and (min-width:768px) {
    .grid--cols-2 {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (min-width:1024px) {
    .grid--cols-3 {
        grid-template-columns: repeat(3, 1fr);
    }
}

/* Testimonials */
.testimonial {
    padding: 3rem;
}

.testimonial__image {
    position: relative;
    margin: 0 3rem;
}

.testimonial__image>img {
    width: 100%;
}

.testimonial__image>.icon-container {
    position: absolute;
    top: 3rem;
    right: -32px;
}

@media screen and (min-width:768px) {
    .testimonial__image {
        margin: 0;
    }

    .testimonial .quote,
    .testimonial .quote__author {
        font-size: 2.4rem;
    }

    .testimonial .quote {
        margin-left: 6rem;
        margin-right: 0;
    }
}

/* Callouts */
.callout {
    padding: 4rem;
    border-radius: 5px;

}
.callout-signup{
    transform: translateY(5rem);
}
.callout--primary {
    color: #fff;
    background: var(--primary-color);
}

.callout__heading {
    color: #fff;
    margin-top: 0;
    font-size: 3rem;
}

.callout .btn {
    align-self: center;
    justify-self: center;
}

.callout__content {
    text-align: center;
}

@media screen and (min-width:768px) {
    .callout .grid--cols-2 {
        grid-template-columns: 1fr auto;
    }

    .callout__content {
        text-align: left;
        width: 100%;
    }

    .callout .btn {
        align-self: center;
        justify-self: start;
        margin: 0 2rem;
    }
}

/* Collapsibles */
.collapsible__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.collapsible__heading {
    margin: 0;
    font-size: 3rem;
    color: #fff;
}

.collapsible__chevron {
    transform: rotate(-90deg);
    transition: all 0.3s ease;
}

.collapsible__content {
    max-height: 0;
    overflow: hidden;
    opacity: 0;
    transition: all 0.5s;

}

.collapsed--expanded .collapsible__chevron {
    transform: rotate(0deg);
}

.collapsed--expanded .collapsible__content {
    max-height: 100vh;
    opacity: 1;
}

/* Blocks */
.block {
    --padding-vertical: 6rem;
    padding: var(--padding-vertical) 2rem;
}

.block__header {
    text-align: center;
    margin-bottom: 4rem;
}

.block__heading {
    margin-top: 0;
}

.block--dark {
    background: #000;
    color: #7b858b;
}

.block--dark .block__heading {
    color: #fff;
}

.block--skewed-right {
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0% 1%, 100% 0%, 100% 100%, 0 90%);
}

.block--skewed-left {
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);
}

.container {
    max-width: 1140px;
    margin: 0 auto;
}

/* Navigation Bar */
.nav {
    background: #000;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 1rem;
    align-items: center;
}

.nav__list {
    width: 100%;
    margin: 0;
}

.nav__item {
    padding: 0.5rem 2rem;
    border-bottom: 1px solid #222;
}

.nav__item>a {
    color: #d2d0db;
    transition: color 0.3s ease;
}

.nav__item>a:hover {
    color: #fff;
}

.nav__toggler {
    opacity: 0.5;
    transition: all 0.15s ease;
    cursor: pointer;
}

.nav.collapsed--expanded .nav__toggler {
    opacity: 1;
    box-shadow: 0 0 0 3px #666;
    border-radius: 5px;
}

.nav__brand {
    transform: translateY(5px);
}

@media screen and (min-width:768px) {
    .nav__toggler {
        display: none;
    }

    .nav__list {
        display: flex;
        font-size: 1.6rem;
        max-height: 100%;
        opacity: 1;
        width: auto;
    }

    .nav__item {
        border: none;
    }
}

/* Hero */
.hero__tagline {
    font-size: 2rem;
    color: #b9c3cf;
    letter-spacing: 1px;
    margin: 2rem 0 5rem;
}

.hero__image {
    max-width: 100%;
}

@media screen and (min-width:768px) {
    .hero {
        padding-top: 0;
    }

    .hero__content {
        align-self: center;
        text-align: left;
    }
}

/* Domain Block */
.block-domain .input-group {
    box-shadow: 0 0 30px 20px #e6ebee;
    border: none;
    margin: 4rem auto;
    max-width: 670px;
}

.block-domain__prices {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 6rem);
    font-size: 2rem;
    font-weight: 600;
    justify-items: center;
    max-width: 700px;
    margin: 0 auto;
    color: var(--headings-color);

}

@media screen and (max-width:375px) {
    .block-domain__prices {
        grid-template-columns: repeat(2, 1fr);

    }
}

@media screen and (min-width:768px) {
    .block-domain__prices {
        grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));

    }
}

/* Plans Block */
.block-plans .grid {
    gap: 8rem 4rem;
}

.block-plans .card {
    max-width: 500px;
    margin: 0 auto;
}

/* Features Block  */
.feature {
    gap: 4rem 2rem;
    margin: 12rem 0;
}

.feature:first-of-type {
    margin-top: 6rem;
}

.feature__heading {
    margin: 1rem 0;
}

.feature__image {
    max-width: 100%;
}

@media screen and (min-width:768px) {
    .feature:nth-of-type(even) .feature__content {
        order: 2;
    }

}

/* Showcase Block */
.showcase__content .media__title {
    color: #fff;
}

.showcase__image>img {
    width: 100%;
}

@media screen and (min-width:768px) {
    .block-showcase .grid {
        grid-template-columns: 50% 50%;
    }

    .showcase__image {

        justify-self: end;
    }

    .showcase__image>img {
        width: auto;
        max-width: 700px;
    }
}

.footer {
    background-color: #232323;
}

.footer a {
    color: #777;
    transition: color 0.3s ease;
}

.footer__section .list {
    margin: 0;
}

.footer a:hover {
    color: #fff;
}

.footer__section {
    padding: 2rem;
    border-bottom: 1px solid #393939;
}

.footer__heading {
    text-transform: uppercase;
    font-weight: 600;
}

.footer__brand {
    margin-top: 5rem;
    text-align: center;
}

.footer__brand img {
    width: 100%;
    max-width: 230px;
}

.footer__copyright {
    font-size: 2.1rem;
    color: #fff;
    opacity: 0.3;
}

@media screen and (min-width:768px) {
    .footer .grid {
        grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    }

    .footer .collapsible__chevron {
        display: none;
    }

    .footer .collapsible__content {
        opacity: 1;
        max-height: 100%;
    }

    .footer__brand {
        order: -1;
        margin-top: 1rem;
    }

    .footer__copyright {
        font-size: 1.5rem;
    }

    .footer__section {
        border: none;
    }

    .footer__heading {
        font-size: 1.6rem;
    }
}

@media screen and (min-width:1300px) {
    .footer__brand {
        font-size: 0.5rem;
        margin-right: 6rem;
    }
}